<template>
  <div class="top" style="text-align:center">
    <img :src="this.$root.IMG_PATH + 'top.jpg'" class="topImgWidth">
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      setIconFileName : "",
      setIconFile : null,
    };
  },
  computed : {
  },
  methods : {
    push : function() {

      let params = new FormData();
      params.append('iconFile', this.setIconFile);
      params.append('iconFileName', this.setIconFileName);

      let config = {
          headers: {'Content-Type': 'multipart/form-data'}
      };
      console.log(params);
      axios.post(this.$root.SERVER_PATH + 'test', params, config)
      .then((response) => {
        console.log(response);
      });
    },
    onFileChange(e) {

      const files = e.target.files || e.dataTransfer.files;
      if (files.length == 0) {
        return;
      }
      let size = files[0].size;
      let type = files[0].type;

      console.log(size + " " + type);

      // this.createImage(files[0]);
      this.setIconFile = files[0];
      this.setIconFileName = files[0].name;

    },
    test : function () {
      let password = "111111@@@";

      let params = new URLSearchParams();

      params.append('password', password);
      axios.post(this.$root.SERVER_PATH + 'tete', params)
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
    test2 : function () {

    },
    initAction : function() {
      axios.get(this.$root.SERVER_PATH + 'isAuth')
      .then((response) => {
        console.log(response.data);

        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        if (this.$root.isLogin) {
          //ログイン済みの場合はメインメニューへ
          this.$router.push("/mainmenu");
        } else {
          this.$root.closePopup();
        }
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {    
      vm.$root.showLoad();  
      vm.twitterId = "";
      vm.twitterName = "";
      vm.twitterProfile = "";
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.twitterId = "";
      vm.twitterName = "";
      vm.twitterProfile = "";
      vm.initAction();
    });
  }
}
</script>

